import React from 'react';
import cx from 'classnames';
import { Popover } from 'antd';
import qs from 'query-string';
import { FormattedMessage } from 'react-intl';
import { getSearchObj } from '@/helpers/location';
import './index.less';

export function getMode() {
  const query = getSearchObj();
  return (query ? query.mode : 'read') as string;
}

export const useModeSwitcher = ({
  className,
}: {
  className?: string;
}): [JSX.Element, string, (v) => void] => {
  const mode = getMode();
  const query = getSearchObj();

  const changeMode = value => {
    if (value === mode) return;
    const {
      pathname,
      hash: currentHash,
      search: currentSearch,
    } = window.location;
    const hash = currentHash === '#/' ? '' : currentHash;
    // const search = qs.stringify({
    //   ...qs.parse(currentSearch),
    //   mode: value,
    // });
    const search = qs.stringify(Object.assign(
      qs.parse(currentSearch),
      { mode: value },
    ))
    
    // window.location.href = `${pathname}?${search}${hash}`;
    window.history.pushState({}, '', `${pathname}?${search}${hash}`);
    window.dispatchEvent(new CustomEvent('modechange', { detail: { mode: value } }));
    console.log('window location, pathname:'+ pathname + ',hash:'+hash+',search:'+search);
  };

  const canPreview = !query.user;

  return [
    <div className={cx('mode-switcher', className)}>
      {mode !== 'edit' && (
        <span className={cx('mode-item')} onClick={() => changeMode('edit')}>
          <FormattedMessage id="" />
        </span>
      )}
      {mode === 'edit' &&
        (canPreview ? (
          <Popover content={<FormattedMessage id="" />}>
            <span>
              <FormattedMessage id="" />
            </span>
          </Popover>
        ) : (
          <span className={cx('mode-item')} onClick={() => changeMode('read')}>
            <FormattedMessage id="" />
          </span>
        ))}
    </div>,
    mode,
    changeMode,
  ];
};
